.ProjectsSection h3 {
    padding-top: 150px;
}
.ProjectsGrid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    transition: all 0.3s;
    max-height: 0;
    overflow: hidden;
    height: auto;
    box-shadow: none;
}

.Expanded {
    max-height: 400vh;
    overflow: visible;
}

.ProjectsSection h4 {
    margin-top: 50px;
    margin-bottom: 10px;
}

.ProjectImage {
    width: calc(100% + 40px);
    margin-left: -20px;
    margin-top: -20px;
}

.ProjectCard h4 {
    margin-top: 0;
    margin-bottom: 0;
}

.ProjectCard p {
    font-size: 16px;
}

.ExternalLinks {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    gap: 20px;
    width: 100%;
    height: auto;
    margin-top: 5px;
    margin-bottom: -10px;
}

.ExternalLinks img {
    width: 100%;
}



@media screen and (max-width: 1000px) {
    .ProjectsGrid {
        grid-template-columns: 1fr 1fr;
    }
    .Expanded {
        max-height: 10000vh;
        overflow: visible;
    }
    
}

@media screen and (max-width: 600px) {
    .ProjectsGrid {
        grid-template-columns: 1fr;
    }
}

